""



export default function ProfilePage () {
    return (
      <div className=" max-w-xl max-w-auto p-6 space-y-6 ">
        <h2 className="text-2xl font-bold mb-4">profile pengguna</h2>
        <form className="space-y-6">
          <div>
            <label
              htmlFor="name"
              className="block mb-1 text-sm font-medium text -gray-700 "
            >nama lengkap</label>
            <input
              type="text"
              name="name"
              className="w-full border rounded px-4 py-2"
            />
          </div>
          <div>
            <label
              htmlFor="email"
              className="block mb-1 text-sm font-medium text -gray-700 "
            >
              email
            </label>
            <input
              type="text"
              name="email"
              className="w-full border rounded px-4 py-2"
            />
          </div>
          <div>
            <label
              htmlFor="number"
              className="block mb-1 text-sm font-medium text -gray-700 "
            >
              {" "}
              nomor telepon
            </label>
            <div className="relative">
              <div className="absolute inset-y-0 left-0 flex items-center pl-3 text-gray-600">+62</div>
              <input
                type="text"
                name="number"
                className="w-full border rounded px-4 py-2 pl-3"
              />
            </div>
          </div>
          <button
          type="submit"
          className="bg-indigo-600 hover:bg-indigo-700 text-white px-4 py-2 rounded disabled:opacity-50"
          >
            simpan perubahan
          </button>
        </form>
      </div>
    );
}